<script>

  /*
  * @component 
  * @author yxc
  * done
  * eg
  * <Pagination
   :showPages="5"
   :curPageNum="curPageNum"
   :rowsTotal="200"
   @pageChange="pageChange"
   ></Pagination>

  * @props { Number } showPages 
  * @props { Number } curPageNum 
  * @props { Number } rowsTotal 
  * @props { Function } pageChange  
  * */

  export default {

    props: [
      "showPages",
      "curPageNum",
      "rowsTotal",
      "pageSize"
    ],

    data(){
      return {
        pageInputNum: "", // 
      }
    },

    computed: {

      // 
      pagesTotal(){
        return Math.ceil(this.rowsTotal / this.pageSize);
      }
    },

    methods: {

      /*
       * @method { Function } getPagesList 
       * @param { Number } curPageNum 
       * @param { Number } showPages 
       * @param { Number } pagesTotal 
       * @return { Array } pagesList 
       * valuetext
       * */
      getPagesList(curPageNum, showPages, pagesTotal){

        //   1
        if(pagesTotal < 1 || !pagesTotal) return [];

        // 
        let pagesList = []; // 

        /*
         *  
         * 1 -- pagesTotal
         * */
        if(showPages > pagesTotal){
          for(let i = 1; i <= pagesTotal; i++){
            pagesList.push({value: i, text: i});
          }

        }else {
          let leftPageNum = Math.floor(showPages / 2); // 
          let rightPageNum = Math.ceil(showPages / 2); // 

          // 
          let leftShortPageNum = leftPageNum - curPageNum + 1;
          leftShortPageNum = leftShortPageNum > 0 ? leftShortPageNum : 0;

          // 
          let tmpRightMaxPageNum = curPageNum + rightPageNum; // 
          let rightShortPageNum = pagesTotal - tmpRightMaxPageNum;
          rightShortPageNum = rightShortPageNum > 0 ? 0 : -(rightShortPageNum);

          // 
          if(!leftShortPageNum){
            // 
            let leftStartPageNum = curPageNum - leftPageNum - rightShortPageNum;
            leftStartPageNum = leftStartPageNum > 0 ? leftStartPageNum : 1;
            for (let i = leftStartPageNum; i<= curPageNum; i++){
              pagesList.push({value: i, text: i});
            }

          }else {
            // 
            for (let i = 1; i <= curPageNum; i++){
              pagesList.push({value: i, text: i});
            }
          }

          // 
          if(!rightShortPageNum){
            // 
            for (let i = curPageNum + 1; i< curPageNum + leftShortPageNum + rightPageNum; i++){
              pagesList.push({value: i, text: i});
            }
          }else {
            // 
            for (let i = curPageNum + 1; i <= pagesTotal; i++){
              pagesList.push({value: i, text: i});
            }
          }

        }

        //  
        if(curPageNum != 1){
          pagesList.unshift({value: curPageNum - 1, text: ""});
        }
        if(curPageNum != pagesTotal){
          pagesList.push({value: curPageNum + 1, text: ""});
        }

        // showPages ...
        // pagesTotal-showPages ...
        if(curPageNum > showPages){
          pagesList.splice(1, 0, {value: curPageNum - showPages, text: "..."});
        }
        if(curPageNum < pagesTotal - showPages){
          let len = pagesList.length;
          pagesList.splice(len - 1, 0, {value: curPageNum + showPages, text: "..."});
        }

        return pagesList;
      },

      /*
       * @method { Function } fixPageInputInRange 1
       * @param { Number } inputPageNum 
       * @param { Number } pagesTotal 
       * @return { Number }tmpInputPageNum 
       * */
      fixPageInputInRange(inputPageNum, pagesTotal){

        let tmpInputPageNum = "";

        if(inputPageNum !== ""){

          let inputPageNumtoNumber = +inputPageNum;

          if(inputPageNumtoNumber < 1){
            tmpInputPageNum = 1;

          }else if(inputPageNumtoNumber > pagesTotal){
            tmpInputPageNum = pagesTotal;

          }else {
            tmpInputPageNum = inputPageNumtoNumber;
          }
        }

        return tmpInputPageNum;
      },

      /*
       * @method { Function } pageInputKeyDown keydown
       * */
      pageInputKeyDown(e){

        let keyCode = e.keyCode;
        if(!(keyCode >= 48 && keyCode <= 57 || keyCode >= 96 && keyCode <= 105 || keyCode == 8)) {

          // return false/true  
          e.preventDefault();
        }
      },

      /*
      * @method selectPage  
      * @param { Number } val 
      * */
      selectPage(val){

        this.curPageNum != val && this.$emit("pageChange", val || 1);
        this.pageInputNum != "" && (this.pageInputNum = "");
      }
    },
  }
</script>

<template>
  <div class="page__root" v-show="rowsTotal">

    <ul class="page__pageList">

      <!--  -->
      <li
          :key="index"
          v-text="item.text"
          @click="selectPage(+item.value)"
          v-for="(item, index) of getPagesList(curPageNum, showPages, pagesTotal)"
          :class="{'page__pagesItem': true, 'page__pagesItem--active': item.value == curPageNum}"
      ></li>
    </ul>

    <!--  -->
    <div class="page__count">{{pagesTotal}}</div>

    <div class="page__to">

      <!--  -->
      <input
        type="text"
        class="page__toInput"
        v-model="pageInputNum"
        @keydown="pageInputKeyDown"
        @keydown.enter="selectPage(pageInputNum)"
        @keyup="pageInputNum=fixPageInputInRange(pageInputNum, pagesTotal)"
    >

      <!--  -->
      <button
        class="page__toBtn"
        @click="selectPage(pageInputNum)"
      ></button>
    </div>
  </div>
</template>

<style lang="scss">
  @import "Pagination.scss";
</style>



