
import Vue from "vue"
import VueHttp from "vue-resource"
import { hex_md5 }from "./md5"
import router from '../../Router'

// storemutations
import store from "../../store/index"
import {
  LOADER_SET_AJAX_NUM,
  LOADER_SHOWHide,
  LOADER_ERROR_SHOWHide,
  DYNAMICPIC_DETAILS_SET } from "../../store/types"

Vue.use(VueHttp);

// vue-resource bug
Vue.http.interceptors.push((request, next) => {
  var timeout;

  //  _timeout 
  if (request._timeout) {

    //  response   status  statusText
    timeout = setTimeout(() => {
      if(request.onTimeout) request.onTimeout(request);
      //request.abort();
    }, request._timeout);
  }

  next((response) => {
    clearTimeout(timeout);
  });
})

/*
 * @method ajax vue-resourceajax 
 * @param { Object } option 
 * */
export function ajax(option) {

  let url = option.url;
    let token = sessionStorage.token;
    let headers = token ?
        {'X-Requested-With': 'XMLHttpRequest', 'Token': token } :
        { 'X-Requested-With': 'XMLHttpRequest' }
  let loading = option.loading === false ? false : true; // loading

  let loaderState = store.state.loader;

  delete option.loading;

  // 1. 
  let defaultConfig = {
    headers,
    emulateJSON: true,
    emulateHTTP: true,
    _timeout: 20000,
    body: {},
    params: {},
    before(){
      if(loading) {
        !loaderState.ajaxNum && store.commit(LOADER_SHOWHide, true); //  loading
        store.commit(LOADER_SET_AJAX_NUM, 1);
      }
    },
    onTimeout: timeoutCallback // 
  };

  // 2. token
  switch (true) {

    //  token
    case  url === "/login.do":
      break;

    default:
        option.params = Object.assign({}, option.params); break;
      break;
  }

  // 3. 
    if (process.env['NODE_ENV'] === 'development') {
        // let devBaseUrl = `http://127.0.0.1:8888${option.url}.json`; // 
        // let devBaseUrl = `http://10.10.20.155:8080${option.url}`; // 
        // let devBaseUrl = `http://127.0.0.1:8888${option.url}`; // 
        // let devBaseUrl = `http://192.168.96.124:8080${option.url}`; // 
        //http://192.168.20.105:8080/
        let devBaseUrl = `http://10.10.20.155:8080${option.url}`; // 
        //let devBaseUrl = `http://192.168.16.56:8080${url}`; // 
        option.url = devBaseUrl;
    }


  // 4. 
  let targetObj = Object.assign({}, defaultConfig, option);

  // 5. ajax
  Vue.http(targetObj)
    .then(

      // 
      response => {
          let data = response.data;

          // if (url === '/indexData/getAreaList.do') {
          //     setTimeout(() => {
          //         // router.push('/login')
          //         location.hash = 'login' //  
          //         return
          //     }, 2000)
          // }

        switch (true){

          // token 
            case data.msg === "error.access.session.invalid" :
                store.commit(LOADER_SET_AJAX_NUM, 0);
                // TODO location.hash 
                router.push('/login')
                break;

          //  
          //  
          case data.success === false :
            timeoutCallback(); break;

          //  
          default:
            targetObj.success(data);

            if(loading) {
              store.commit(LOADER_SET_AJAX_NUM, -1);
              //  loading
              !loaderState.ajaxNum && store.commit(LOADER_SHOWHide, false);
            }

            break;
        }
      },

      // 
      timeoutCallback
    );

  // 6. 
  function error() {
    console.log(`${url} `);

    if(!loading) return ;

    // 
    // showError 
    if(loaderState.showError) {
      store.commit(LOADER_SET_AJAX_NUM, -1);

    }else {
      //  showError  
      store.commit(LOADER_ERROR_SHOWHide, true);
    }
  }

  // 7. 
  function timeoutCallback() {
    console.log(`API ${url} `);

    if(!loading) return ;

    store.commit(LOADER_ERROR_SHOWHide, true);
    // store.commit(LOADER_SET_AJAX_NUM, -1);
    // !loaderState.ajaxNum && store.commit(LOADER_SHOWHide, false);
  }
}

