/*
* @module loader 
* @author yxc
* */

import {
  LOADER_SET_AJAX_NUM,
  LOADER_SHOWHide,
  LOADER_ERROR_SHOWHide
} from "../types"

const loader = {

  state: {
    ajaxNum: 0, // ajax
    show: false, // Loader
    showError: false, // 
  },

  mutations: {
    [LOADER_SET_AJAX_NUM](state, val){
      let ajaxNum = state.ajaxNum;
      switch (val){
        case -1:
          ajaxNum --;

          //     ajaxNum 0
          ajaxNum < 0 && (ajaxNum = 0);
          break;

        case 0:
          ajaxNum = 0;
          break;

        case 1:
          ajaxNum ++;
          break;

        default:
          break;
      }

      //     
      if(!ajaxNum) {
        state.show = false;
        state.showError = false;
      }

      state.ajaxNum = ajaxNum;
    },

    [LOADER_SHOWHide](state, val){
      state.show = val;
    },

    [LOADER_ERROR_SHOWHide](state, val){

      state.show = val;
      state.showError = val;

      if(!val) {
        state.ajaxNum --;
        state.ajaxNum < 0 && (state.ajaxNum = 0);
      }
    }
  }
}

export default loader;



