<script>
     /*
   * @component // 
   * @author yxc
   * @date 2016/11/12 0012
   * */

     import Tab from "../../../Tab/Tab.vue"

    export default {
       props: [
         "tabIndex" // tab
       ],

       data(){
         return {
           tabs: ["", "", ""]
         }
       },

      methods: {
        tabChange(tabIndex){
          this.$emit("tabChange", tabIndex + 1);
        }
      },

      components: { Tab }
    }

</script>

<template>
  <div class="warnStaticDimensions__root">
    <h2 class="warnStaticDimensions__title"></h2>
    <Tab :tabs="tabs"
         :tabIndex="tabIndex"
         @tabChange="tabChange"
    ></Tab>
  </div>
</template>

<style lang="scss">
  @import "WarnStaticRiskDimensions.scss";
</style>



