<script>
  /*
   * @component // 
   * @author yxc
   * */

  import NoResNotice from "../../../../NoResNotice/NoResNotice.vue"

  export default {
    props: ["list"],

    data(){
      return {
        isHasRes: true // 
      }
    },

    watch: {
      list(){
        this.isHasRes = this.list.length ? true : false;
      }
    },

    components: {
      NoResNotice
    }
  }
</script>

<template>
  <div class="detailBasicBusinessUpdate__root">
    <div class="detailBasicBusinessUpdate__wrap">

      <template v-if="isHasRes">
        <h2 class="detailBasicBusinessUpdate__title">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </h2>
        <ul class="detailBasicBusinessUpdate__list">
          <li
            v-for="item of list"
            class="detailBasicBusinessUpdate__item">
            <span v-text="item.changeItems"></span>
            <span v-text="item.contentBeforeChange"></span>
            <span v-text="item.contentAfterChange"></span>
            <span v-text="item.changeDate"></span>
          </li>

        </ul>
      </template>

      <NoResNotice
        v-show="!isHasRes"
        msg=""
      ></NoResNotice>
    </div>
  </div>
</template>

<style lang="scss">
  @import "DetailBasicBusinessUpdate.scss";
</style>



