/*
 * @module dynamicPicDetails 
 * @author yxc
 * */

import { DYNAMICPIC_DETAILS_SET } from "../types"

const dynamicPicDetails = {
  state: {
    renderData: {} //
  },

  mutations: {
    [DYNAMICPIC_DETAILS_SET](state, data) {
      state.renderData = data;
    }
  }
}

export default dynamicPicDetails;



