<script>
     /*
   * @component // 
   * @author yxc
   * @date 2016/11/14 0014
   * */

     import Tab from "../../../../Tab/Tab.vue"
     import NoResNotice from "../../../../NoResNotice/NoResNotice.vue"

    export default {
      props: [
        "oneAndDimension" // 
      ],

      data(){
        return {
          tabIndex: 0, // 
          dimensionName: "", //   
          isHasRes: true // 
        }
      },

      computed: {
        dimensionData(){

          let dimension = "";

          if(this.tabIndex == 0) {
            dimension = "one";
            this.dimensionName = "";

          }else {
            dimension = "two";
            this.dimensionName = "";
          }

          let tmpDimensionData = this.oneAndDimension[dimension];

          if(!tmpDimensionData) {
            this.isHasRes = true;
            tmpDimensionData = [];

          }else {
            this.isHasRes = tmpDimensionData.length ? true : false;
          }

          return tmpDimensionData;
        }
      },

      methods: {
         tabChange(tabIndex){
           this.tabIndex = tabIndex;
         }
      },

       components: {
        Tab,
        NoResNotice
      }
    }
</script>

<template>
  <div class="detailsRelatesTable__root">

    <Tab
      :tabIndex="tabIndex"
      :tabs="['', '']"
      @tabChange="tabChange"
    ></Tab>

    <div
      v-show="isHasRes"
      class="detailsRelatesTable__table"
    >

      <div class="detailsRelatesTable__wrap">

        <!--  -->
        <h2 class="detailsRelatesTable__th">
          <span class="detailsRelatesTable__name"></span>
          <span class="detailsRelatesTable__date"></span>
          <span class="detailsRelatesTable__money"></span>
          <span class="detailsRelatesTable__addr"></span>
          <span class="detailsRelatesTable__represent"></span>
          <span class="detailsRelatesTable__industry"></span>
        </h2>

        <ul class="detailsRelatesTable__list">
          <li
            v-for="item of dimensionData"
            class="detailsRelatesTable__item"
          >
            <span class="detailsRelatesTable__name" v-text="item.companyName"></span>
            <span class="detailsRelatesTable__date" v-text="item.esDate"></span>
            <span class="detailsRelatesTable__money" v-text="item.regCap"></span>
            <span class="detailsRelatesTable__addr" v-text="item.address"></span>
            <span class="detailsRelatesTable__represent" v-text="item.frName"></span>
            <span class="detailsRelatesTable__industry" v-text="item.companyIndustry"></span>
          </li>
        </ul>
      </div>

      <p class="detailsRelatesTable__count">
        {{dimensionName}} <span v-text="dimensionData.length"></span> 
      </p>

    </div>

    <NoResNotice
      v-show="!isHasRes"
      msg=""
    ></NoResNotice>
  </div>
</template>

<style lang="scss">
  @import "DetailsRelatesTable.scss";
</style>



