<script>
     /*
   * @component ///  
   * @author yxc
   * @date 2016/11/14
   * done
   * */

    export default {

      methods: {

        // 
        picScaleUp(){
          seajs.use("/static/libs/relativeGraph/assets/script/main/dynamic.js", function (main){
            main.zoomAdd();
          });
        },
        picScaleDown(){
          seajs.use("/static/libs/relativeGraph/assets/script/main/dynamic.js", function (main){
            main.zoomSub();
          });
        },

        /*
        * @method handleClcik  
        * BTN__HIDE__WORD 
        * */
        handleClcik(){
          window.BTN__HIDE__WORD = true;
        }
      }
    }
</script>

<template>
  <ul class="warnStaticRiskTool__root">
    <li class="warnStaticRiskTool__item"
        id="btn_hidden_word"
        @click="handleClcik"
    >
      <i class="iconfont icon-wenzi"></i>
    </li>
    <li class="warnStaticRiskTool__item"
        @click="picScaleUp"
    >
      <i class="iconfont icon-jiahao"></i>
    </li>
    <li class="warnStaticRiskTool__item"
        @click="picScaleDown"
    >
      <i class="iconfont icon-minus"></i>
    </li>
  </ul>
</template>

<style lang="scss">
  @import "WarnStaticRiskTool.scss";
</style>



